import Vue from 'vue';
import VueRouter from 'vue-router';
import TestVux from "../components/TestVux";
//@表示src路径
import TopicList from "../components/TopicList";
import Login from '@/components/Login';
import Center from '@/components/Center';
import News from '@/components/News';
import Logout from '@/components/Logout';
import Topic from "@/components/Topic";
import User from "@/components/User";
import Create from "@/components/Create";
import Collect from "@/components/Collect";
import Edit from "@/components/Edit";
//全局注册
Vue.use(VueRouter);

//定义路由信息对象
const routes = [
  {path: '/', redirect: '/topiclist/all'},
  // {path: '/testvux', component: TestVux, name: 'testvux'},
  {path: '/topiclist/:name', component: TopicList, name: 'topiclist', props: true},
  //详情页面
  {path: '/topic/:id', component: Topic, props: true},
  //登录页面
  {path: '/Login', component: Login},
  //个人中心
  {path: '/Center', component: Center},
  // 用户中心
  {path:'/user/:loginname',component: User,props: true},
  //消息通知
  {path: '/News', component: News},
  //登出
  {path: '/Logout', component: Logout},
  //发表新话题
  {path:'/Create',component: Create},
  //用户收藏
  {path:'/user/:loginname/collect',component:Collect,props: true},
  //编辑
  {path:'/topic/:id/edit',component: Edit,props: true}
]

//创建路由管理器
const router = new VueRouter({
  routes
})

//对外输出
export default router;



