export default {
  name: "Center",
  computed: {
    loginname:function () {
      return this.$store.state.loginname;
    }
  },
  created:function () {
    //跳转到用户界面，使用编程式路由跳转
    //console.log('aa');
    // this.$router.push,这个方法会向history添加一个记录，点击后退返回到上一个界面
    // this.$router.push('/user/+this.loginname');此方法不会，点击后返回上上个界面
    // 上一个页面的记录不存在
    this.$router.replace('/user/'+this.loginname);
  }
}



